
<template>
    <form action="" class="login-mod" onsubmit="return false" @submit="login">
        <div class="logo">
            <img src="../images/logo.png" alt="">
        </div>
        <div class="input-area">
            <div class="username inputbox">
                <input type="text" placeholder="输入账号" v-model="data.username">
            </div>
            <div class="password inputbox">
                <input type="password" placeholder="输入密码" v-model="data.password">
            </div>
            <div class="btn">
                <input type="submit" value="登录">
            </div>
        </div>
    </form>
</template>
<script>
import axios from "axios";

export default {
    props:["lastUrl"],
    data(){

        return{
            data:{
            username:"",
            password:""
            }

        }
    },

    methods:{
        login(){
            axios.post("/zhuiszhu/user/lgn",this.data).then(({data})=>{
               this.open(data.text,"警告","warning")
                if(data.code==200){
                    this.$emit("logininfo",this.data.username)
                    this.$cookies.set("username",this.data.username,600)
                    if(this.lastUrl==""){
                        this.$router.push({name:'home'})
                    }else{
                         this.$router.replace({path:this.lastUrl})
                    }
                }
            })
        },
        open(txt,title,type) {
            this.$alert(txt, title, {
            confirmButtonText: '确定',
            type: type
            });
         }
    },
 
}
</script>

<style lang="less" scoped>

    .login-mod{
        width: 100%;
        background: rgba(255,255,255, .7);
        .logo{
            text-align: center;
            padding: 5% 0;
        }
        .input-area{
            width: 50%;
            margin: auto;
            .inputbox{
               width: 100%;
               height: 50px;
               margin-top: 5%;
               overflow: hidden;
                input{
                    width: 70%;
                    float: right;
                    border: none;
                    border-radius: 0;
                    font-size: 14px;
                    height: 100%;
                    padding: 0 30px;
                    border-radius: 10px;
                    outline: none;
                }
            }
             .username{
                    input{
                         background:rgb(255, 255, 255) url(../images/user.png) 5px center no-repeat;
                    }
                }
                .password{
                    input{
                         background:#fff url(../images/pwd.png) 5px center no-repeat;

                    }
            }
            .btn{
                text-align: center;
                padding: 10% 0;
                input[type="submit"]{
                    margin: auto;
                    width: 50%;
                    padding: 5% 0;
                    border: none;
                    outline: none;
                    background: red;
                    color: #fff;
                    border-radius: 50px;
                    cursor: pointer;

                 }
            }
            
               
           
        }

    }

</style>





