<template>
   <div class="right">
       <div class="box">
        <el-popover
            placement="top-start"
            title="退出哦"
            width="200"
            trigger="hover"
            content="我都不知道怎么退出登录，搞个锤子">
            <el-button slot="reference" @click="loginout">注销</el-button>
        </el-popover>
        </div>

   </div>
</template>

<script>
import axios from "axios"
export default {
    methods:{
        loginout(){
            this.$emit("getInfo",false)
            this.$cookies.remove("username")
            axios.get("/zhuiszhu/user/exit").then(({data})=>{
                console.log(data)
            })
            this.$router.go(0);
            this.$router.replace("home");
        }
    }
}
</script>
<style scoped>
    .right{
        height: 100vh;
        background: #dddddd
    }
    .box{
        width: 200px;
        margin: 200px auto;
    }
    .el-button{
        background:red;
        color: white;
    }
</style>




