<template>
    <div class="maylike2">
        <span>猜你喜欢</span>
        <ul>
            <router-link tag="li" v-for="(item,i) in like" :key="i" :to="{name:'detail',params:{goodsID:item._id}}">
                <img :src="item.img" alt="">
                <p>￥{{item.price}}</p>
            </router-link>
           
        </ul>
    </div>
</template>
<script>
import axios from "axios"

export default {
    data(){
        return{
            like:[]
        }
    },
    mounted(){
        axios.get("/zhuiszhu/goods/getList?page=1&pageSize=50").then(({data})=>{
            let list=[];
            for(let i =0;i<4;i++){
             let num = Math.floor(Math.random()*50);
             let flag= list.some((obj)=>{
                 return obj==num
             })
                if(flag){ i--;}else{ list.push(num)}
            }
            list.map(item=>{
                this.like.push(data.list[item])
            })
         })
    }
}
</script>

<style lang="less" scoped>
    .maylike2{
        float: left;
        width: 25%;
        margin-left: 2%;
        margin-top: 2%;
        span{
            display: inline-block;
            border-left: 5px solid red;
            padding-left: 20px;
            margin: 10px 0;
            
        }
        ul{
            display: flex;
            flex-wrap: wrap;
           width: 100%;
           text-align: center;
            li{ 
                width: 43%;
                cursor: pointer;
                img{
                    width:100%;
                    height: calc(100vh/5.5);
                }
                p{
                    white-space: nowrap;
                    text-overflow: ellipsis;
                    overflow: hidden;
                    font-size: 14px;
                    padding: 5px 0;
                    color: red;
                }
            
            }
            li:nth-child(2n){
                margin-left: 6%;
            }
            
        }
    }
</style>






