<template>
    <div class="right">
        <div class="search-main-box">
        <form action="" onsubmit="false" class="search-box" @submit="search(input)">
        <el-input v-model="input" placeholder="请输入内容"></el-input><el-button @click="search(input)" icon="el-icon-search"></el-button>
        </form>
        <div class="result">
            <ul>
                <router-link tag="li" :to="{name:'detail',params:{goodsID:item._id}}"  v-for="(item,i) in list" :key="i">
                   <a>{{i+1}}·{{item.name}}</a><span>￥{{item.price}}</span>
                </router-link>
                
            </ul>
            <p v-show="result">搜索不到内容，来看看这些</p>
        </div>
        <maylike2 v-show="list.length<=0"/>
        </div>
    </div>
</template>
<script>
import axios from "axios"
import maylike2 from "../components/maylike2.vue"
export default {
    data(){
        return{
            input:"",
            list:[],
            result:false
        }
    },
    mounted(){
        let {key} = this.$route.query;
        if(key){
            this.search(key)        
        }
    },
    methods:{
        search(input){
            this.$router.push({name:'search',query:{key:input}})
            axios.get("/zhuiszhu/goods/search",{params:{search:input,pageSize:100}}).then(({data})=>{
                this.list=data.list;
                if(this.list.length<=0){this.result=true}else{this.result=false}
            })
        }
    },
    components:{
        maylike2
    }
}
</script>

<style lang="less" scoped>
    .search-main-box{
        height: 100vh;
    }
    .search-box{
        width: 100%;
        margin-top: 6%;
        .el-input{
         width: 30% !important;
        }
    }
    .result{
        margin-top: 2%;
            p{
                margin-left: 6%;
             }
        ul{
            width: 30%;
            li{
                width: 100%;
                padding: 5px;
                margin-top: 2%;
                a{
                    text-decoration: underline;
                    width: 80%;
                    display: inline-block;
                   white-space: nowrap;
                   text-overflow: ellipsis;
                   overflow: hidden;
                   font-size: 16px;
                   color: blue;
                }
                span{
                    float:right;
                    color: red;
                }
                p{
                    font-size: 12px;
                    color: gray;
                }
            }
        }
    }
</style>




