
<template>
   <div class="right">

        <div class="login-box">
            <div class="box-top">
                <router-link active-class="inHere" tag="span" :to="{name:data.name}" v-for="data in logList" :key="data.name">{{data.txt}}</router-link>
            </div>
            <router-view @logininfo="getUser" :lastUrl="lastUrl"/>
        </div>

   </div>
</template>

<script>

export default {
    data(){
        return {
            logList:[
                {txt:"登录",name:"log"},
                {txt:"注册",name:"reg"}
            ],
            lastUrl:""
              
           
        }
    },
    methods:{
        getUser(msg){
            this.$emit("getInfo",msg)
        }
      
    },
    beforeRouteEnter:(to,from,next)=>{ 
        next(vm => {
             vm.lastUrl=from.path;
        })
    }
}
</script>

<style lang="less" scoped>
    .right{
        height: calc(100vh/1);
        // background:url(../images/login.jpg) no-repeat;
        // background-size: 60% 100%;
        background: url(../images/bg1.jpg) ;
        // background: #a7a1a1;
        background-size: cover;
        .login-box{

            box-shadow:0px 2px 10px 2px rgb(170, 149, 149);
            width: 50%;
            margin: 10% auto;  
            border-radius: 10px;
            overflow: hidden;
            .box-top{
                width: 100%;
                text-align: center;
                background: #f5f5f5;
                box-shadow:0px 0px 5px 1px rgb(170, 149, 149);
                span{
                    font-size: 20px;
                    display: inline-block;
                    width: 50%;
                    padding: 2% 0;
                    cursor: pointer;
                    &.inHere{
                        background: red;
                        color: #fff;
                    }
                }
            } 
              
        }
    }
</style>




