<template>
   <div class="right">
       <div class="type-nav">
           <dl class="goodstype">
               <dt><i class="el-icon-menu"></i>类型</dt>
               <router-link @click.native="change" tag="dd" v-for="item in goodstype" :key="item.txt" :to="{path:item.name}">{{item.txt}}</router-link>
           </dl>
          
           <dl class="sort">
               <dt><i class="el-icon-edit"></i>排序</dt>
               <dd>
                    <el-radio-group v-model="radio1" size="mini" @change="change">
                    <el-radio-button label="false">无</el-radio-button>
                    <el-radio-button label="discount">折扣</el-radio-button>
                    <el-radio-button label="price">价格</el-radio-button>
                    </el-radio-group>
               </dd>
               <dd>
                    <el-radio-group v-model="radio2" size="mini" @change="change">
                    <el-radio-button label="false">无</el-radio-button>
                    <el-radio-button label="1">升序</el-radio-button>
                    <el-radio-button label="-1">降序</el-radio-button>
                    </el-radio-group>
               </dd>
                
           </dl>
           
       </div>
       <div class="goods-list">
           <ul class="goods-box">
               <li v-for="item in goodslist" :key="item._id" >
                   <router-link :to="{name:'detail',params:{goodsID:item._id}}">
                   <img :src="item.img" alt="">
                   </router-link>
                    <el-rate v-model="value1"></el-rate>
                   <p class="price">￥{{item.price}}<del class="discount">￥{{item.price*10/item.discount | two}}</del></p>
                   <p class="name">{{item.name}}</p>
               </li>
           </ul>
 
            <div class="block">
                <el-pagination
                    background
                    @current-change="change"
                    :current-page.sync="currentpage"
                    layout="prev, pager, next"
                    :total="count*10">
                </el-pagination>
            </div>
       </div>
   </div>
</template>

<script>
import axios from "axios"

export default {
    data(){
        return {
            goodstype:[
                {txt:"全部",name:"goodslist"},
                {txt:"衬衫",name:"goodslist?type=shirt"},
                {txt:"T恤",name:"goodslist?type=t-shirt"},
                {txt:"礼服",name:"goodslist?type=dress"},
                {txt:"西服",name:"goodslist?type=suit"},
                {txt:"西裤",name:"goodslist?type=trousers"},
                {txt:"西装",name:"goodslist?type=suit_all"}
            ],
            goodslist:[],
            type:false,
            currentpage:1,
            count:1,      //总数
            radio1:"false",
            radio2:"false",
            value1:4
           
        }
    },
    mounted(){

        this.change()
        
    },
    methods:{
        change(){
            let {type} = this.$route.query;
            if(type!=this.type){
                this.currentpage=1
            }
            this.type=type;
            let newdata=`page=${this.currentpage}&pageSize=6`
            if(this.type){
            newdata=`type=${this.type}&page=${this.currentpage}&pageSize=6`
            }
            if(this.radio1!="false"){
                newdata+=`&sort=${this.radio1}`
            }
            if(this.radio2!="false"){
                this.radio2*=1;
                newdata+=`&isAsc=${this.radio2}`
            }
            console.log(newdata)
            axios.get("/zhuiszhu/goods/getList?"+newdata).then(({data})=>{
                this.goodslist=data.list;
                this.count=Math.ceil(data.count/6);
            })
        },

    },
    filters:{
        two(value){
            return value.toFixed(2);
        }
    }

}
</script>


<style lang="less" scoped>  
   .block{
       margin-top: 5%;
       margin-left: 25%;
       padding-bottom: 5%;
    }
    .type-nav{
        float: left;
        width: 15%;
        height: 100vh;
        background: #f5f7fa;
        text-align: center;
        overflow: hidden;
        font-size: 14px;
       .goodstype,.sort{
           margin-top: 50%;
           dt{
               font-size: 18px;
               margin-bottom: 20%;
               font-weight: 400;
           }
           dd{
               margin-top: 10%;
               color: #959595;
               font-weight: 400;
               cursor: pointer;
               &.router-link-exact-active{
                   color: red;
                   font-size: 18px;
               }
           }
       }
    }
    .goods-list{
        width: 85%;
        float: right;
        height: 100%;   
        .goods-box{
            display: flex;
            flex-wrap: wrap;
            width: 100%;
            margin-top: 2%;
            li{
                width: 22%;
                margin-left: 5%;
                padding: 10px 0;
                img{
                    width:100%;
                   height: calc(100vh/3.4);
                    // border-radius: 10px;
                }
                .el-rate{
                    padding:5px 0;
                }
                .price{
                    font-size: 18px;
                    color: rgb(51, 189, 74);
                    margin-top: 2%;
                }
                .name{
                    width: 100%;
                    height: 8%;
                    white-space:nowrap;
                    text-overflow: ellipsis;
                    overflow: hidden;
                    font-weight: bold;
                    font-size: 14px;
                    margin-top: 2%;
                }
                .discount{
                    float: right;
                    font-size: 14px;
                    line-height: 30px;    
                    color: rgb(172, 158, 158);
                }
            }
        }

    }
</style>




