<template>
   <form action="" class="login-mod" onsubmit="return false" @submit="reg" >
        <div class="logo">
            <img src="../images/logo.png" alt="">
        </div>
        <div class="input-area">
            <div class="username inputbox">
                <input type="text" placeholder="6-12位非数字开头的字母数字组成" @keyup="checkReg('user',userReg)" v-model="data.username">
                <span>
                    <img src="../images/gou.png" v-show="flag1==1">
                    <img src="../images/cha.png" v-show="flag1==0">
                </span>
            </div>
            <div class="password inputbox">
                <input type="password" placeholder="6-12位字母数字组成" @keyup="checkReg('pwd',pwdReg)" v-model="data.password">
                <span>
                    <img src="../images/gou.png" v-show="flag2==1">
                    <img src="../images/cha.png" v-show="flag2==0">
                </span>
            </div>
             <div class="password inputbox">
                <input type="password" placeholder="确认密码" @keyup="pwdCheck" v-model="pwdsure">
                <span>
                    <img src="../images/gou.png" v-show="flag3==1">
                    <img src="../images/cha.png" v-show="flag3==0">
                </span>
            </div>
            <div class="btn">
                <input type="submit" value="注册">
            </div>
        </div>
    </form>
</template>

<script>
import axios from "axios";

export default {
    data(){

        return{
            userReg:/^[A-Za-z_][A-Za-z_0-9]{5,11}$/,
            pwdReg:/^[a-zA-Z0-9_-]{6,12}$/,
            pwdsure:"",
           flag1:null,
           flag2:null,
           flag3:null,
           flag:true,
            data:{
                username:"",
                password:""
            }
        }


    },
    methods:{
        checkReg(target,reg){
            if(target=="user"){
                if(reg.test(this.data.username)){
                   this.flag1=1;
                }else{
                    this.flag1=0
                }
            }else{
                if(reg.test(this.data.password)){
                     this.flag2=1
                     this.flag=false;
                }else{
                     this.flag2=0
                }
            }
           
        },
        pwdCheck(){
            if(this.pwdReg.test(this.pwdsure)){
                if(this.pwdsure == this.data.password){
                    this.flag3=1;
                }else{
                    this.flag3=0;
                }
            }else{
                this.flag3=0;
            }
            
        },
        reg(){
            if(this.flag1 == 1 && this.flag2 == 1 && this.flag3 == 1){
                let newdate={
                    username:this.data.username,
                    password:this.data.password
                }
                 axios.post("/zhuiszhu/user/reg",newdate).then(({data})=>{
                    // console.log(data)
                    this.open(data.text,"信息","warning")
                    if(data.code==200){
                        this.$router.push({name:'log'})
                    }
                 })
            }else{
                this.open("检查您的错误","错误","warning")
            }
        },
        open(txt,title,type) {
            this.$alert(txt, title, {
            confirmButtonText: '确定',
            type: type
            });
         }
    }
}
</script>

<style lang="less" scoped>

    .login-mod{
        width: 100%;
        background: rgba(255,255,255, .7);
        .logo{
            text-align: center;
            padding: 5% 0;
        }
        .input-area{
            width: 50%;
            margin: auto;
            .inputbox{
               width: 100%;
               height: 50px;
               margin-top: 5%;
               position: relative;
                input{
                    width: 70%;
                    float: right;
                    border: none;
                    border-radius: 0;
                    font-size: 14px;
                    height: 100%;
                    padding: 0 30px;
                    border-radius: 10px;
                    outline: none;
                }
                span{
                    position: absolute;
                    display: inline-block;
                    right: 15px;
                    top: 10px;

                    img{
                        width: 30px;
                        height: 30px;
                    }
                }
            }
             .username{
                    input{
                         background:rgb(255, 255, 255) url(../images/user.png) 5px center no-repeat;
                    }
                }
                .password{
                    input{
                         background:#fff url(../images/pwd.png) 5px center no-repeat;

                    }
            }
            .btn{
                text-align: center;
                padding: 10% 0;
                input[type="submit"]{
                    margin: auto;
                    width: 50%;
                    padding: 5% 0;
                    border: none;
                    outline: none;
                    background: red;
                    color: #fff;
                    border-radius: 50px;
                    cursor: pointer;

                 }
            }
            

               
           
        }

    }

</style>





