// 路由配置  

//  0.
import Vue from "vue";
import VueRouter from "vue-router";
Vue.use(VueRouter);

import home from "./views/home.vue"
import search from "./views/search.vue"
import exit from "./views/exit.vue"
import detail from "./views/detail.vue"
import shopcar from "./views/shopcar.vue"
import goodslist from "./views/goodslist.vue"
import login from "./views/login.vue"
import loginMode from "./components/loginMod.vue"
import registerMode from "./components/registerMod.vue"




const routes = [{
        path: "/",
        redirect: "/home"
    },
   
    {
        path: "/home",
        name: "home",
        component: home
    },
   
    {
        path: "/login",
        name: "login",
        component: login,
        redirect:"/login/log",
        children:[
            {path:"/login/log",name:"log",component:loginMode},
            {path:"/login/reg",name:"reg",component:registerMode}
        ]
    },
   
    {
        path: "/goodslist",
        name: "goodslist",
        component: goodslist
    },
   
    {
        path: "/shopcar",
        name: "shopcar",
        component: shopcar
    },
    {
        path:"/detail/:goodsID",
        name:"detail",
        component:detail
    },
    {
        path:"/exit",
        name:"exit",
        component:exit
    },
    {
        path:"/search",
        name:"search",
        component:search
    }

]



// 3 
const router = new VueRouter({
    routes,
    mode: "hash",
});


// 4 导出挂载 

export default router;


