import router from "./scripts/router";

import Vue from 'vue'

import App from './App.vue';

import ElementUI from "element-ui";
import VueCookies from 'vue-cookies'
Vue.use(VueCookies)
Vue.use(ElementUI);
Vue.config.productionTip = false
new Vue({
    render: h => h(App),
    router,
}).$mount('#app')


