
<template>
    <div class="right">
        <div class="banner">
            <el-carousel :interval="5000" arrow="always">
                <el-carousel-item v-for="item in detail.banner" :key="item">
               <img :src="item" alt="">
                </el-carousel-item>
            </el-carousel>
        </div>
        <div class="detial-box">
            <span class="line"></span>
            <p class="nowPrice">￥{{detail.price}}<del class="bfPrice">￥{{detail.price*10/detail.discount | two}}</del></p>
            <p class="goodsname">{{detail.name}}</p>
            <div class="rate-block">
            <el-rate
                v-model="value"
                :colors="['#99A9BF', '#F7BA2A', '#FF9900']">
            </el-rate>
            </div>
            
            <p class="intro">
                {{detail.detail}}
            </p>
            <div class="button-box">
                 <el-input-number v-model="num1" :min="1" :max="10" label="控制器"></el-input-number>
                <span class="stack">库存:{{detail.stock}}</span>
            </div>
            <el-button type="success" icon="el-icon-goods" class="addBtn" @click="addcar(detail._id)">加入购物车</el-button>     
            <el-button type="primary" icon="el-icon-back" @click="back">返回上一页</el-button>
        </div>
        <maylike/>

        <div class="hot-goods">
            <div class="hot-top">
                <span>热门商品</span>
            </div>
            <el-carousel :interval="5000" arrow="always">
                <el-carousel-item  v-for="(one,i) in hotList" :key="i">
                <router-link :to="{name:'detail',params:{goodsID:one.goodsID}}">
                <img :src="one.banner" alt="">
                </router-link>
                </el-carousel-item>
            </el-carousel>
        </div>

      
        <div class="detail-img">
            <img src="../images/detail.png" alt="">
            <img :src="img" v-for="(img,i) in detail.imgs" :key="i" alt="">
        </div>

        <div class="top-btn"  v-show="isShow>700">
            <el-button type="danger" icon="el-icon-arrow-up" circle @click="backTop"></el-button>
        </div>
    </div>
</template>

<script>
import axios from 'axios';
import maylike from "../components/maylike.vue"
import shopcarVue from './shopcar.vue';
export default {
    data(){
        return{
            detail:{},
            value:4,
            num1:1,
            activeName:null,
            hotList:[],
            isShow:0
        }
    },
    watch:{
         $route(to,from){
           if(to.path!=from.path){
               let {goodsID}=this.$route.params;
                axios.get("/zhuiszhu/goods/getGoods?goodsID="+goodsID).then(({data})=>{
                    if(data.goods!=this.detail){
                        this.detail=data.goods;
                    }
                })
           }
        }
    },

    mounted(){
        document.documentElement.scrollTop=0;
        window.addEventListener('scroll', this.handleScroll, true)
        let {goodsID}=this.$route.params;
        axios.get("/zhuiszhu/goods/getGoods?goodsID="+goodsID).then(({data})=>{
            this.detail=data.goods;
        })
        axios.get("/zhuiszhu/goods/getHot").then(({data})=>{
            this.hotList=data.list;
        })
    },
    methods:{
        back(){
            this.$router.go(-1)
        },
        handleScroll() {
           this.isShow=document.documentElement.scrollTop
           
        },

        addcar(goodsID){
             axios.get("/zhuiszhu/shopCar/getList").then(({data})=>{
                 let shopcar=data.list;
                 let flag=false;
                 if(shopcar){
                    for(let i=0;i<shopcar.length;i++){
                            if(shopcar[i].goodsID==goodsID){
                                flag=true;
                                let nowNum=shopcar[i].number*1+this.num1*1
                                axios.get("/zhuiszhu/shopCar/update",{params: {goodsID,number:nowNum}}).then(({data})=>{
                                    if(data.code==400){
                                            this.open2();
                                        }else if(data.code==200){
                                            this.open("成功","加入成功","success")
                                        }else{
                                            this.open("警告","未知错误","warning")
                                        }
                                })
                            }
                    }
                  }
                 if(!flag){
                     axios.get("/zhuiszhu/shopCar/addToCar?goodsID="+goodsID+"&number="+this.num1).then(({data})=>{
                        if(data.code==400){
                            this.open2();
                        }else if(data.code==200){
                            this.open("成功","加入成功","success")
                        }else{
                            this.open("警告","未知错误","warning")
                        }
                     })
                 }
             })
        },
        open(title,msg,type) {
            this.$notify({
            title: title,
            message: msg,
            type: type
            })
        },
        open1(txt,title,type) {
            this.$alert(txt, title, {
            confirmButtonText: '确定',
            type: type
            });
         },
         backTop(){
             document.documentElement.scrollTop=0;
         },
         open2() {
            this.$confirm('您未登录或者登录信息已经过期, 是否登录?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
            }).then(() => {
                this.$router.push({name:'login'})
            }).catch(() => {
            this.$message({
                type: 'info',
                message: '已取消登录'
            });          
            });
        }
           
    },
    filters:{
        two(value){
            return value.toFixed(2);
        }
    },
    components:{maylike}
}
</script>

<style lang="less">

    .top-btn{
        position: fixed;
        bottom: 50px;
        right: 50px;
        width: 4%;
        img{
            width: 100%;
        }
    }
    .maylike{
        margin-top: 6% !important;
        width: 13% !important;
        float: right !important;
        margin-right: 2%;   
        ul{
            display: block !important;
            li{
                width: 100% !important; 
            }
            li:nth-child(2n){
                margin-left: 0 !important;
            }
        }
    }
    .banner{
        width: 35%;
        height: 60%;
        float: left;
        margin-top: 6%;
        margin-left: 2%;
        overflow: hidden;
        .el-carousel__container{
            height: calc(100vh/1.66);
        }
        .el-carousel__item img {
            width: 100%;
            height: 100%;
        }

    }
    .detial-box{
        margin-top: 6%;
        width: 40%;
        height: 60%;
        float: left;
        padding-bottom: 20%;
        margin-left: 5%;
        .line{
            display: inline-block;
            width: 50%;
            border: 1px solid #fbb710;
            vertical-align: top;            
        }
        .nowPrice{
            color: #fbb710;
            font-size: 22px;
            font-weight: 600;
            margin-bottom: 2%;
            .bfPrice{
                margin-left: 10%;
                font-size: 16px;
                font-weight:200;
                color: rgb(94, 83, 83);
            }
        }
        .goodsname{
            font-size: 22px;
            font-weight: bold;
            margin-bottom: 2%; 
            letter-spacing:1px;

        }
        .rate-block{
            padding: 2%;
        }
        .intro{
            font-size: 14px;
            letter-spacing:1px;
             display: -webkit-box;
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 3;
             overflow: hidden;
            line-height: 2.1;
             color: #6b6b6b;
        }
        .button-box{
            margin-top: 5%;
            span{
                color: rgb(170, 157, 157);
                font-size: 12px;
            }
        }
        .addBtn{
            margin-top: 5%;
            width: 30%;
            height: 10%;
        }

    }
    .detail-img{
        float: left;
        width:80%;
        img{
            width: 100%;
        }
    }
    .ping{
       float: left;
        width: 75.8%;
        // border-radius:50px;
        border: 2px solid rgb(246, 243, 237);
        padding: 2%;
        span{
           font-size: 12px;
            display: inline-block;
        }
        .el-progress-bar{
        width: 40% !important;
        }
    }
    .el-carousel__item h3 {
    color: #475669;
    font-size: 14px;
    opacity: 0.75;
    line-height: 200px;
    margin: 0;
  }
  
  .hot-goods{
      float: left;
      width: 80%;
    //   height: 30%;
    .hot-top{
        margin-bottom: 5px;
        border-top: 1px dashed #d2d2d2;
        padding: 5px 0 0 0;
        span{
            display: inline-block;
            border-left: 10px solid red;
            padding-left: 10px;
            color: red;
            font-size: 20px;
        }
    }
    .el-carousel__container{
        height: 100vh/2.5;
    }
      img{
          width: 100%;
          height: 100%;
      }

  }
  
</style>




