<template>
    <div class="right">
       <ul class="item-box cl">
           <router-link tag="li" v-for="(item) in list" :key="item.name" :class="item.name" :to="{name:'goodslist',query:{type:item.type}}">
               <img :src="item.img" alt="">
               <div class="hide-box">
                   <span class="line"></span>
                   <span class="lowprice">低至{{item.low}}</span>
                   <h3 class="goodtype">{{item.txt}}</h3>
               </div>
           </router-link>
          
       </ul>
    </div>
</template>
<script>
export default {
    data(){
        return{
            list:[
                {txt:"衬衫",name:"part1",img:require("../images/chenshan.jpg"),show:0,low:100,type:"shirt"},
                {txt:"T恤",name:"part2",img:require("../images/txu.jpg"),show:0,low:50,type:"t-shirt"},
                {txt:"礼服",name:"part3",img:require("../images/lifu.jpg"),show:0,low:120,type:"dress"},
                {txt:"西服",name:"part4",img:require("../images/xifu.jpg"),show:0,low:200,type:"suit"},
                {txt:"西裤",name:"part5",img:require("../images/xiku.jpg"),show:0,low:98,type:"trousers"},
                {txt:"西装",name:"part6",img:require("../images/xizhuang.jpg"),show:0,low:199,type:"suit_all"}
            ],
        }
    },
    methods:{
      
    }
}
</script>

<style lang="less" scoped>
    .item-box{
        background: rgb(240, 220, 220);
        height: 100%;
        position: relative;
        .part1,.part2,.part3,{float: left;width: 33.333%;
            img{width: 100%;height: 100%;}
            position: relative;
            overflow: hidden;
        }

        .part4,.part5{float: right;width: 33.333%;
            img{width: 100%;height: 100%;}
            position: relative;
            overflow: hidden;
        }
        .part1{
            height: 50%;
        }
        .part2{
            height:55%;
        }
        .part3{
            height: 45%;
        }
        .part4{
            height: 55%;
        }
        .part5{
            height: 45%;
        }
        .part6{
            position:absolute;
            height: 50%;
            width: 33.333%;
             img{width: 100%;height: 100%;}
            overflow: hidden;
            bottom: 0;


        }
        .hide-box{
            width: 100%;
            height: 100%;
            position: absolute;
            top: 0;
            transition: .3s;
            span{
                display: block;
            }
            .line{
                width: 10%;
                border: 2px solid rgb(34, 189, 11);
                margin-top: 8%;
                margin-left: 8%;
            }
            .lowprice{
                font-weight: bold;
                color: rgb(88, 82, 82);
                margin-left: 8%;
                margin-top: 2%;
               
            }
            .goodtype{
                margin-left: 8%;
            }
        }
        .hide-box:hover{
            background: rgba( 0, 0, 0, .7);
        }
        .hide-box:hover .lowprice,.hide-box:hover .goodtype{
            color: white;
        }
    

    }
   

</style>




