<template>
  <div id="app">
    <link rel="stylesheet" href="https://unpkg.com/element-ui/lib/theme-chalk/index.css">
    <Nav :user='msg'/>
      <router-view @getInfo="transport"></router-view>
    <Foot/>
  </div>
</template>

<script>
import Nav from "./scripts/components/nav.vue"
import Foot from "./scripts/components/footer.vue";
export default {
  
  name: 'app',
  data(){
    return{
      msg:false,
    }
  },
  components: {
     Nav,
     Foot
  },
  methods:{
    transport(msg){
      this.msg=msg
    }
  }

}
</script>
<style>

    *{margin: 0;padding: 0;}
    /* body{font-family: "微软雅黑"} */
			li{list-style: none;}
			a{text-decoration: none;}
			img{vertical-align: top;}
			.cl:after{content: "";height: 0;clear: both;display: block;}
			.cl{zoom: 1;}
    #app{
    }
    .right{
      width: 83%;
      float: right;
      
    }
    .footer{
    
    background: #252525;
    float: left;
}

</style>



