<template>
    <div class="left">
        <div class="logo-box">
            <router-link :to="{name:'home'}">
                 <img src="../images/logo.png" alt="">
            </router-link>
            <span v-show="false">{{user}}</span>
        </div>
        <ul class="nav-box">
            <router-link v-for="car in list" :to="{name:car.name}" tag="li"  :key="car.txt">
                <span></span>
                <i :class="car.class"></i>
                {{car.txt}}
            </router-link>
            <div class="discount">
                <a href="/">% Discount %</a>
            </div>
            <div class="discount">
                <a href="/">新品</a>
            </div>
        </ul>
    </div>  
</template>

<script>
export default {
    props:["user"],
    data(){
        return {  
            list:[
                {txt:"主页",name:"home",class:"el-icon-menu"},
                {txt:"商品",name:"goodslist",class:"el-icon-goods"},
                {txt:"登录",name:"login",class:"el-icon-bell"},
                {txt:"购物车",name:"shopcar",class:"el-icon-sold-out"},
                {txt:"搜索",name:"search",class:"el-icon-search"},
            ]          
        }
    },
    methods:{
    
    },
    beforeUpdate(){
        if(this.user){
            this.list[2].txt="欢迎:"+this.user;
            this.list[2].name="exit";
        }
    },
    mounted(){
        let username=this.$cookies.get("username") 
        if(username){
            this.list[2].txt="欢迎:"+username;
            this.list[2].name="exit";
        }
    }
}
</script>


 <style lang="less" scoped>
    .discount{
        width: 80%;
        background: #fbb710;
        text-align: center;
        transition: .8s;
        margin-top: 15%;
        padding: 8% 0;
        a{
            font-size: 18px;
            color: white;
        }
    }
    .discount:hover{
        background: #000;
        transition: .8s;
    }
    .discount:last-child{
        background: #000 !important;
    }
    .discount:last-child:hover{
        background: #fbb710 !important;
        transition: .8s;
    }
    .left{
        width: 17%;
        background:#fff;
        float: left;
        // box-shadow: 1px -1px 10px 1px  rgb(196, 140, 140);
        .logo-box{
            margin: 15% auto;
            width: 50%;
            img{
                max-width: 100%;
            }
        }
        .nav-box{
            width: 50%;
            margin:30% auto;
            li{
                padding: 10% 0;
                font-size: 14px;
                cursor: pointer;
                position: relative;
                span{
                      display: inline-block;
                        width: 0;
                        border: 0px solid orange;
                        background: orange;
                         position: absolute;
                        left: -50%;
                        bottom: 50%;
                }
                &.router-link-active{
                    span{
                        width: 20%;
                        border: 2px solid orange;

                    }
                }
            }
            li:hover span{
                width: 20%;
                border: 2px solid orange;
                transition: .5s
            }
        }
    }

</style>

    






