<template>
   <div class="right">
        <div class="shopCar-box">
            <div class="car-box">
                <h2>购物车</h2>
                  <el-button type="primary" icon="el-icon-back" circle @click="back"></el-button>
                <table class="goodsCar">
                    <thead>
                        <tr>
                            <th>
                                全选
                                <el-checkbox v-model="checkAll" @change="selectAll"></el-checkbox>
                            </th>
                            <th></th>
                            <th>名称</th>
                            <th>价格</th>
                            <th>数量</th>
                            <th>小计</th>
                            <th>
                                <el-button type="danger" @click="open3">批量删除</el-button>
                            </th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr class="tip" v-show="list.length<1">
                            <td colspan="7">
                                 ~购物车空空如也~
                            </td>
                            </tr>
                        <tr v-for="(item,i) in list" :key="i">
                            <td><el-checkbox v-model="check[i]" @change="selectOne(i)"></el-checkbox></td>
                            <td>
                                <router-link :to="{name:'detail',params:{goodsID:item.goodsID}}"> 
                                <img :src="item.img" alt="">
                                </router-link>
                            </td>
                            <td class="name">{{item.name}}</td>
                            <td class="price">￥{{item.price}}<br/><del>￥{{item.price*10/item.discount | two}}</del></td>
                            <td>
                                 <el-input-number @change="updateCar(item.goodsID,item.number)"  v-model="item.number"  :min="1" :max="100" label="描述文字"></el-input-number>
                            </td>
                            <td class="price">￥{{item.price*item.number}}</td>
                            <td>
                                <el-button @click="delectOne(item.goodsID,i)" type="danger" icon="el-icon-delete" circle></el-button>
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>
            <div class="count-box">
                    <h3>总计</h3>
                    <p>费用:<span>{{total}}</span></p>
                    <p>运费:<span>0</span></p>
                    <p>总价:<span>{{total}}</span></p> 
                    <el-button type="success" class="checkout" @click="checkout">结算</el-button>
            </div>
            <maylike2/>

        </div>
        <el-dialog title="订单信息" :visible.sync="dialogTableVisible">
        <el-table :data="gridData">
            <el-table-column property="date" label="日期" width="250"></el-table-column>
            <el-table-column property="name" label="姓名" width="500"></el-table-column>
            <el-table-column property="money" label="价格" width="100"></el-table-column>
        </el-table>
        <div slot="footer" class="dialog-footer">
            <el-input style="width:auto"
            v-model="total"
            :disabled="true">
            </el-input>
            <el-button @click="dialogTableVisible = false">取 消</el-button>
            <el-button type="primary" @click="delectAll">确 定</el-button>
        </div>
        </el-dialog>
   </div>

</template>

<script>
import axios from "axios"
import maylike2 from "../components/mayLike2.vue"
export default {
    data(){
        return {
            list:[],
            check:[],
            checkAll:false,
            total:0,
            gridData:[
            ],
            dialogTableVisible:false,
        }
    },
    components:{
        maylike2
    },
    mounted(){
        axios.get("/zhuiszhu/shopCar/getList").then(({data})=>{
            console.log(data.list)
            if(data.code==400){
                this.open2();
            }else if(data.code==200){
                
                this.list = data.list;
                for(let i=0;i<data.list.length;i++){
                    this.check.push(false);
                }
            }
        })
    },
    watch:{
  
    },
    methods:{
        // clearDate(){
        //     this.gridData=[];
        //     this.dialogTableVisible = false
        // },
        back(){
            this.$router.go(-1);
        },
        checkout(){
        this.gridData=[];
        let deleteList=[];
          this.check.map((item,i)=>{
               if(item==true){
                deleteList.push(i)
               }
           })
           if(deleteList.length<1){
               this.open("快选一个心仪的宝贝吧","警告","warning");
           }else{
               let date =new Date();
               for(let i=0;i<deleteList.length;i++){
                   let index = deleteList[i]
                   this.gridData.push({date:date.toLocaleDateString( ),name:this.list[index].name,money:this.list[index].price})
               }
               this.dialogTableVisible=true;

           }
        },
        delectOne(goodsID,index){
            axios.get("/zhuiszhu/shopCar/del?goodsID="+goodsID).then(({data})=>{
                if(data.code==200){
                    this.list.splice(index,1)
                    this.check.splice(index,1)
                     this.$message({
                        message: '删除成功',
                        type: 'success'
                    });
                }else if(data.code==400){
                    this.open2();
                }else{
                    this.open("未知错误","警告","warning");        
                } 
            })
        },
        delectAll(){
            this.dialogTableVisible=false;  
            let nowList=[];
            let nowCheck=[];
            for(let i=0;i<this.check.length;i++){
                if(this.check[i]==true){
                    axios.get("/zhuiszhu/shopCar/del?goodsID="+this.list[i].goodsID).then(({data})=>{
                        if(data.code==400){
                            // this.open("您的登录信息已过期","警告","warning");
                            this.open2();
                        }else if(data.code !=200){
                            this.open("未知错误","警告","warning");
                        }else{
                            this.$message({
                                message: '删除成功',
                                type: 'success'
                             });
                        }
                    })  
                }else{
                    nowList.push(this.list[i])
                    nowCheck.push(this.check[i])
                }
            }
            this.list=nowList;
            this.check=nowCheck;
            if(this.check.length==0){
                this.checkAll=false;
            }
        },
     
        selectOne(index){
            let flag = true;
            for(let i=0;i<this.check.length;i++){
                if(this.check[i]==false){
                    flag=false
                }
            }
            this.checkAll=flag;
        },
        selectAll(){
            let flag=this.checkAll;
            console.log(flag)
            for(let i=0;i<this.check.length;i++){
                this.check[i]=flag
            }
        },
        updateCar(goodsID,number){
            axios.get("/zhuiszhu/shopCar/update",{params: {goodsID,number}}).then(({data})=>{
               if(data.code==400){
                 this.open2();
               }else if(data.code!=200){
                   this.open("未知错误","警告","warning");
               }
            })
        },
        open(txt,title,type) {
            this.$alert(txt, title, {
            confirmButtonText: '确定',
            type: type
            });
         }, open2() {
            this.$confirm('您未登录或者登录信息过期, 是否登录?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
            }).then(() => {
                this.$router.push({name:'login'})
            }).catch(() => {
                this.$router.go(-1)
            });
        }, open3() {
            this.$confirm('您确定删除?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
            }).then(() => {
                this.delectAll();
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                });          
            });
        }
    },
    updated(){
      
        let total=0
        for(let i= 0;i<this.check.length;i++){
            if(this.check[i]==true){
               total+=this.list[i].price*this.list[i].number
            }
        }
        this.total="￥"+total;
    },
     filters:{
        two(value){
            return value.toFixed(2);
        }
    }


}
</script>


<style lang="less">
    .shopCar-box{
        min-height: 100vh;
        .car-box{
            float: left;
            width: 70%;
            margin-top: 3%;
             min-height: 100vh;

            h2{
                font-size: 30px;
                margin-bottom: 30px;
                color: #242424;
            }
            table{
                        .el-checkbox__inner{
                            border-color: rgb(171, 153, 204);
                        }
                        .el-input-number{
                            width: 120px;
                        }
                margin-top: 2%;
                width: 100%;
                border-collapse: collapse;
                overflow: hidden;
                    thead{
                        tr{
                             th{
                                   width: 14.28%;
                                   background: #f5f7fa;
                                   font-size: 12px;
                                   padding: 10px 0;
                                   text-transform:capitalize;
                                }
                        }
                    }
                    tbody{

                        .tip{
                        font-size: 20px;
                        font-weight:400;
                        line-height: 200px;
                        height: 200px;
                        text-align: center;
                        border-bottom: none;
                        }
                        tr{
                            align-items:center;
                            text-align: center;
                            border-bottom: 1px dashed #e1e2e4;
                            td{
                                width: 14.28%;
                                padding: 5px;
                                height: 130px;
                                &.name{
                                    font-size: 12px;
                                    width: 20%;
                                    overflow: hidden;
                                }
                                &.price{
                                    font-size: 14px;
                                    color: olivedrab;
                                    del{
                                        font-size: 12px;
                                        color: #888b92;
                                    }
                                }
                                img{
                                    max-width: 80%;
                                    max-height: 100%;   
                                }
                            }
                        }
                    }
                
               
            }
        }
        .count-box{
            width: 25%;
            background: #f5f7fa;
            float: left;
            margin-top: 7.5%;
            margin-left: 2%;
            padding: 10px 0 ;
            h3{
                margin: 5%;
            }
            p{
                margin-left: 5%;
                padding: 5px 0;
                font-size: 14px;
                span{
                    float: right;
                    margin-right: 5%;
                    color: orange;
                }
            }
            .checkout{
                width: 80%; 
                margin-left: 10%;
                margin-top:10%;
            }
        }
    }
     
</style>






