<template>
    <div class="footer cl">
        <div class="foot-box">
            <div class="foot-logo">
                <img src="../images/logo2.png" alt="">
            </div>
            <div class="foot-copy">
                <p>Copyright ©2018 All rights reserved | by Colorlib - More </p>
                <p>Amado 阿妈多 -made by lambor(许凌波)</p>
            </div>
            <ul class="foot-nav">
                <!-- <li>主页</li>
                <li>商品</li>
                <li>登录</li>
                <li>购物车</li> -->
                <router-link tag="li" v-for="car in list" :key="car.name" :to="{name:car.name}">{{car.txt}}</router-link>
            </ul>
        </div>
    </div>
</template>
<script>
export default {
     data(){
        return {  
            list:[
                {txt:"主页",name:"home"},
                {txt:"商品",name:"goodslist"},
                {txt:"登录",name:"login"},
                {txt:"购物车",name:"shopcar"}
            ]          
        }
    }
}
</script>

<style lang="less" scoped>

    .footer{
        width: 100%;
        height: 250px;
        .foot-box{
            position: relative;
            width: 60%;
            margin: 5% auto 0 auto;

            .foot-copy{
                p{
                    font-size: 12px;
                    margin-top: 5px;
                    color: rgba(255, 255, 255, 0.4);
                }
            }
            .foot-nav{
                display: flex;
                position: absolute;
                width: 50%;
                top: 0;
                right: 0;
                li{
                    width: 25%;
                    text-align: center;
                    color: #fff;
                    cursor: pointer;
                     &.router-link-active{
                    color: yellow;
                }
                }
            }
        }
    }
</style>





